* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* overflow: hidden; */
}

body {
    margin: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    /* background-image: url("./bg-doodle.png");
    background: contain;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat; */
}

#root,
.App {
    width: 100%;
    height: 100%;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    position: relative;
    border: 0.5px solid white;
    color: white;
    width: 95%;
    height: 93%;
}

.logo-text {
    margin: 1rem;
    margin-bottom: 0.5rem;
    font-family: "Monoton";
    font-size: 4.2vw;
}

.logo-subtext {
    margin-left: 1rem;
    margin-top: -1rem;
    font-family: "Kodchasan";
}

.Navbar {
    display: inline-block;
    margin-left: 1rem;
    margin-top: 3rem;
}


a {
    text-decoration: none;
    color: #fff;
    font-family: "Kodchasan";
    font-size: 1rem;
}

li {
    list-style: none;
    margin-bottom: 1rem;
}



nav ul li a:hover {
    color: #b0b0b0;

}

.Home, 
.Faqs,
.Contact,
.Projects {
    animation: fadeInAnimation ease 2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.home-intro-text {
    font-family: "klee one";
    font-size: 1rem;
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 1rem;
    max-width: 175px;
    /* text-align: center; */
}

.Contact{
    margin: 1rem;
    max-width: 20rem;
    position: absolute;
    right: 1%;
    bottom: 0;
    font-size: 1rem;
}

.external-link-icon {
    width: 0.7rem;
    height: 0.7rem;
    margin-left: 0.5rem;
}

.project-list {
    position: absolute;
    top: 0;
    right: 3%;
    max-height: 100%;
    max-width: 70%;
    overflow-y: auto;
}

.project-title {
    text-align: right;
    font-size: 4vw;
}

.project-title-subtext {
    text-align: right;
    font-size: 1rem;
}

.project {
    margin-bottom: 1.5rem;
}

.project:hover {
    color: #b0b0b0;
}

.project-links {
    margin-top: 12rem;
}

.faq-list {
    position: absolute;
    top: 0;
    right: 3%;
    height: 100%;
    max-width: 50%;
    overflow-y: auto;
}

.question-text,
.answer-text {
    font-family: "Kodchasan";
    font-size: 1rem;
}

.question-text {
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.faqs {
    margin-top: 12rem;
}

.faq {
    margin-bottom: 3rem;
}

.canvas {
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 100%;
    background-color: #000;
}

.Form{
    width: 45%;
    position: absolute;
    right: 1%;
    bottom: 0;
    font-size: 1rem;
}

.Form form{
    margin: 1rem;
    display: flex;
    flex-direction: column;
}
.Form label{
    color: white;
    font-family: monospace;
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}

.Form input{
    font-family: monospace;
    margin: 0;
    font-size: 1.2rem;
    color: white;
    width: 100%;
    border: 1px solid #777;
    border-radius: 15px;
    background: none;
    margin-bottom: 1rem;
    padding: 1rem;
}
.Form textarea{
    font-size: 1.2rem;
    padding: 1rem;
    color: white;
    width: 100%;
    border: 1px solid #777;
    border-radius: 15px;
    background: none;
}

.btn--submit{
    margin-top: 2rem;
    background-color: white;
    color: #000;
    font-weight: bolder;
    font-size: 1rem;
    width: 100%;
    align-self: center;
    border: 1px solid #777;
    border-radius: 15px;
    margin-bottom: 1rem;
    padding: 1rem;
    cursor: pointer;
}
.btn--submit:hover{
    background-color: rgb(157, 157, 157);
}
.btn--submit:active{
    background-color: rgb(78, 78, 78);
}

/* media questies */

@media only screen and (max-width: 767px) {

    /* Styles for mobile devices */

    .logo-text {
        font-size: 2.5rem;
    }

    .logo-subtext {
        font-family: "Kodchasan";
        font-size: 1rem;
    }

    .home-intro-text {
        font-size: 0.9rem;
        max-width: 150px;
        text-align: left;

    }

    .Navbar a {
        font-size: 1rem;
    }

    a {
        font-size: 0.8rem;
    }

    .external-link-icon {
        width: 0.5rem;
        height: 0.5rem;
        margin-left: 0.3rem;
    }

    .contact-links {
        font-size: 0.5rem;
    }

    .question-text,
    .answer-text {
        font-family: "Kodchasan";
        font-size: 0.7rem;
    }
    .question-text {
        font-size: 0.8rem;
        margin-bottom: 1rem;
    }
    .faq {
        margin-bottom: 2rem;
    }

    /* form */
    .Form{
        width: 60%;
    }
    .Form form{
        margin: 1rem;
    }
    .Form label{
        font-size: 1rem;
    }
    
    .Form input{
        font-size: 1rem;
    }
    .Form textarea{
        font-size: 1rem;
    }
    
    .btn--submit{
        font-size: 0.9rem;
        padding: 0.9rem;
    }

}


